<template>
    <div style="height: 100%;width: 100% ;">
        <v-card to>
            <v-bottom-nav
                    :active.sync="bottomNav"
                    :color="color"
                    :value="true"
                    height="56px"
                    to
                    shift

            >
                <v-btn dark to="/">
                    <span>主页</span>
                    <v-icon>accessibility</v-icon>
                </v-btn>

                <v-btn dark to="/music">
                    <span>音乐</span>
                    <v-icon>music_note</v-icon>
                </v-btn>
                <v-btn dark>
                    <span>Book</span>
                    <v-icon>book</v-icon>
                </v-btn>

                <v-btn dark>
                    <span>Image</span>
                    <v-icon>image</v-icon>
                </v-btn>
            </v-bottom-nav>
        </v-card>
    </div>

</template>

<script>
    export default {
        name: "commonFooter",
        data() {
            return {
                bottomNav: 0
            }
        },
        computed: {
            color() {
                var color = null;
                switch (this.bottomNav) {
                    case 0:
                        color = 'green'
                        break;
                    case 1:
                        color = 'teal'
                        break;
                    case 2:
                        color = 'brown'
                        break;
                    case 3:
                        color ='indigo'
                        break;
                }
                this.$store.state.appTopFooterColor = color;
                return color;
            }
        },

    }
</script>

