<template>
        <v-container style="padding: 0;height: 100%">
            <v-layout justify-space-between align-center row fill-height >
                <v-flex xs3>
                    <v-btn
                            slot="activator"
                            color="blue-grey"
                            @click="clickPernsonal"
                            dark
                    >
                        <v-icon>account_circle</v-icon>
                    </v-btn>
                </v-flex>
                <v-flex xs6 style="text-align: center">

                    <v-combobox
                            v-model="searchVal"
                            chips
                            label="I use chips"
                    ></v-combobox>
                    <!--<span>{{$store.getters.appPageName}}</span>-->
                </v-flex>
                <v-flex xs3 style="text-align: right">
                    <v-menu transition="scale-transition"  >
                        <v-btn slot="activator" dark  color="blue-grey"><v-icon>search</v-icon></v-btn>
                        <v-list>
                            <v-list-tile v-for="n in 5" :key="n">
                                <v-list-tile-title v-text="'显示' + n"></v-list-tile-title>
                            </v-list-tile>
                        </v-list>
                    </v-menu>
                </v-flex>

            </v-layout>

        </v-container>
</template>

<script>
    export default {
        data: () => ({
            sheet: false,
            isEditing : true,
            searchVal : "",
            states : [],
        }),
        methods: {
            clickPernsonal() {
                this.$emit("drawerEvent");
            }
        },
        watch : {
            searchVal (val){
                console.log(val)
                if(!val ){
                    return ;
                }
                var url = this.$store.state.api.music.searchMusicList.url;
                var data = this.$store.state.api.music.musicLists.data;
                data.s = val;
                var vm = this;
                this.axios.get(url, {
                    params: data
                }).then(data => {
                    var musicArr = [];
                    data.data.data.forEach((item,index) => {
                        var musicDetail =  {
                            title: item.name,
                            artist: '推荐',
                            src: item.url,
                            pic: item.pic,
                            lrc: item.lrc
                        };
                        musicArr.push(musicDetail);
                    })
                    vm.$store.state.musicList = musicArr;
                    vm.$router.push({
                        path: '/music/' + "search"
                    })
                }).catch(err => {
                    console.log(err);
                })
            }
        }
    }
</script>

<style scoped>
   .v-toolbar__content{
       padding: 0;
   }
</style>
